import React, { useState } from 'react';
import { FormControl, InputLabel, Input, FormLabel, FormGroup, FormControlLabel, TextField, Button } from '@material-ui/core';


const HomePage = () => {
    const [state, setState] = useState({
        businessType: '',
        zipCode: ''
    });

    const handleChange = name => event => {
        setState({ ...state, [name]: event.target.value })
    }

    return (
        <FormControl>
            <InputLabel htmlFor="my-input">Business Name</InputLabel>
            <Input id="my-input" onChange={handleChange('businessName')} value={state.businessType} />
        </FormControl>
        // <FormControl>
        //     <FormLabel component="legend">Enter a Business Type and Zip Code to Search</FormLabel>
        //     <FormGroup>
        //         <TextField onChange={handleChange('businessType')} value="" />

        //         <TextField onChange={handleChange('zipCode')} value="" />

        //         <Button>
        //             Submit
        //         </Button>
        //     </FormGroup>
        // </FormControl>
    )
}

export default HomePage;